bers-list li:not(:last-child){margin-bottom:5px}.chat-menu__bottom .chat-menu__new-message .chat-menu__members-list.active{display:block}.chat-menu__bottom .chat-menu__new-message .chat-menu__members:hover,.chat-menu__bottom .chat-menu__new-message .chat-menu__members.active{background-color:#e7b26d}.chat-menu__bottom .chat-menu__new-message .chat-menu__member-me{border-bottom:1px solid #000}.chat-menu__bottom .chat-menu__new-message .chat-menu__new-message-confirm{flex-grow:1;max-width:70px;max-height:40px;padding:0 15px;cursor:pointer;border:1px solid #000;border-top-right-radius:20px;border-bottom-right-radius:20px;outline:none;background-color:white}.chat-menu__bottom .chat-menu__new-message .chat-menu__new-message-confirm img{width:100%;height:100%}.chat-menu__bottom .chat-menu__new-message .chat-menu__new-message-confirm.straightened{border-top-right-radius:0}.chat-menu__bottom .chat-menu__new-message .chat-menu__new-message-confirm:hover{background-color:#b2f6fa}.chat-menu__bottom .chat-menu__new-message .chat-menu__new-message-confirm:active{background-color:#499599}

